@import "~antd/dist/antd.css";
body {
  background-image: url("./assets/bg.png");
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.giticon {
  margin: 3%;
  font-size: xx-large;
  color: grey;
}

.nav {
  font-weight: bold;
}

.about {
  padding: 10vh 15vw;
}

.about h2 {
  margin: 20% 10px 0px;
}

.experience {
  text-align: left;
  margin: 15vh 10vw;
  font-size: 18px;
}

.contact {
  margin: 20px 10px;
}

.date {
  text-align: right;
}

.welcome {
  font-size: 72px;
}

.type {
  font-size: 42px;
}

footer p {
  margin: 25px
}

@media screen and (max-width: 800px) {
  .experience {
    margin: 15vh 0vw;
    font-size: 15px;
  }
}